.clearfix:after,.login-options ul li:after{clear:both}.clearfix:after,.clearfix:before{content:"";display:table}.container-fluid{padding:0}.login-options{border-bottom:1px solid #75b8e5;background-color:#276187;overflow:hidden}.login-options ul li{float:right;display:inline-block;padding:10px 30px;list-style:none}.login-options ul li:after,.login-options ul li:before{content:"";display:table}.login-options ul li a{color:#fff}.navbar{border:0;margin-bottom:0;border-bottom:1px solid #75b8e5;border-radius:0;background:url(../img/navbg.jpg) top center no-repeat}.navbar .navbar-brand{color:#fff;padding-top:18px}.navbar .nav{float:none;display:inline-block;text-align:center;padding-top:4px}.navbar .nav li a{color:#fff;margin-bottom:3px}.navbar .nav li a:hover{background:0 0;border-bottom:3px solid #bca51d;margin-bottom:0}.navbar .nav li a:focus{background:0 0}.navbar .cover-menu-content{display:none;position:fixed;top:0;left:0;z-index:99999999;width:100%;height:100%;background-color:#00101c;opacity:.75}.navbar .cover-menu-content .icon-close{display:inline-block;font-size:3em;position:absolute;top:30px;right:60px;cursor:pointer}.navbar .cover-menu-content .menu-title{margin-top:100px;margin-bottom:40px;font-size:4.5em;color:#bca51d}.navbar .cover-menu-content ul li a{display:inline-block;padding:10px 0;font-size:2.5em;font-weight:300;color:#fff}.navbar .minified-menu{display:none}.video-container{height:800px;background:url(../img/bg.jpg) top center no-repeat;background-size:cover}.video-container .vid{margin-top:205px;background:linear-gradient(rgba(174,214,249,.5),rgba(255,255,255,0));box-shadow:3px -3px 5px rgba(0,0,0,.35)}.video-container video{position:relative;width:100%;margin-top:15px;margin-bottom:20px}.video-container .video-controls .play-pause{position:absolute;top:80px;left:calc(37.5%);display:inline-block;width:100px;height:100px;background:url(../img/videoplay.png) no-repeat}.subpage-image{height:400px;background:url(../img/subbg.jpg) top center no-repeat;background-size:cover}@media screen and (max-width:992px){.navbar .minified-menu{display:inline-block}.navbar .standard-menu{display:none}}@media screen and (max-width:768px){.search-content{display:none}}